import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ViewDispensariesService } from '../../view-dispensaries.service';

import "rxjs/add/operator/takeWhile";

@Component({
  selector: 'extracts-strains',
  templateUrl: './extracts-strains.component.html',
  styleUrls: ['./extracts-strains.component.css']
})
export class ExtractsStrainsComponent implements OnInit, OnDestroy {

  private alive = true;

  constructor(private viewDispensariesService: ViewDispensariesService) { }

  ngOnInit() {
    this.viewDispensariesService.getDispensarie()
      .takeWhile(() => this.alive)
      .subscribe((res) => {
        console.log("res>>> ", res);
      })
  }

  ngOnDestroy() {
    this.alive = false;
  }

}



