import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { ViewDispensariesComponent } from './main/view-dispensaries.component';
import { DetailsDispensariesComponent } from './details/details-dispensaries.component';
import { HomeDispensariesComponent } from './home/home-dispensaries.component';
import { ReviewDispensariesComponent } from './review/review-dispensaries.component';
import { ProductsDispensariesComponent } from './products/products-dispensaries.component';
import { StrainsDispensariesComponent } from './strains/strains-dispensaries.component';
import { MarijuanaStrainsComponent } from './strains/marijuana/marijuana-strains.component';
import { ExtractsStrainsComponent } from './strains/extracts/extracts-strains.component';
import { EdiblesStrainsComponent } from './strains/edibles/edibles-strains.component';
import { OralStrainsComponent } from './strains/oral/oral-strains.component';
import { TopicalsStrainsComponent } from './strains/topicals/topicals-strains.component';

const ViewDispensariesRoutes: Routes = [
  {
    path: ':id', component: ViewDispensariesComponent, children: [
      {
        path: '', redirectTo: 'home', pathMatch: 'full'
      },
      {
        path: 'home', component: HomeDispensariesComponent
      },
      {
        path: 'details', component: DetailsDispensariesComponent
      },
      {
        path: 'reviews', component: ReviewDispensariesComponent
      },
      {
        path: 'strains', component: StrainsDispensariesComponent, children: [
          {
            path: '', redirectTo: 'marijuana', pathMatch: 'full'
          },
          {
            path: 'marijuana', component: MarijuanaStrainsComponent
          },
          {
            path: 'extracts', component: ExtractsStrainsComponent
          },
          {
            path: 'edibles', component: EdiblesStrainsComponent
          },
          {
            path: 'oral', component: OralStrainsComponent
          },
          {
            path: 'topicals', component: TopicalsStrainsComponent
          },
        ]
      },
      {
        path: 'products', component: ProductsDispensariesComponent
      },
    ]
  },
];

@NgModule({
  imports: [RouterModule.forChild(ViewDispensariesRoutes)],
  exports: [RouterModule]
})
export class ViewDispensariesRoutingModule { }



