import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { TradeService } from '../../../../common/trade/trade.service';
import { VoucherService } from '../../../../common/voucher/voucher.service';
import { ViewDispensariesService } from '../view-dispensaries.service';

import "rxjs/add/operator/takeWhile";

@Component({
  selector: 'app-details-dispensaries',
  templateUrl: './details-dispensaries.component.html',
  styleUrls: ['./details-dispensaries.component.css']
})
export class DetailsDispensariesComponent implements OnInit, OnDestroy {

  private alive = true;
  private dispensarie: any;


  constructor(private viewDispensariesService: ViewDispensariesService) {

  }

  ngOnInit() {
    this.viewDispensariesService.getDispensarie()
      .takeWhile(() => this.alive)
      .subscribe((res) => {
        this.dispensarie = res;
      })
  }

  ngOnDestroy() {
    this.alive = false;
  }

}



