import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { Subscription } from 'rxjs/Subscription';
import { TradeService } from '../../../../common/trade/trade.service';
import { VoucherService } from '../../../../common/voucher/voucher.service';
import "rxjs/add/operator/takeWhile";

@Component({
  selector: 'strains-dispensaries',
  templateUrl: './strains-dispensaries.component.html',
  styleUrls: ['./strains-dispensaries.component.css']
})
export class StrainsDispensariesComponent implements OnInit, OnDestroy {

  private menuList: [{ title: string, path: string }];
  private vouchers: Array<any>;
  private indica: Array<any>;
  private sativa: Array<any>;
  private edibles: Array<any>;
  private extracts: Array<any>;
  private alive = true;

  constructor(
    private route: ActivatedRoute,
    private tradeService: TradeService,
    private voucherService: VoucherService,
    private router: Router) {

    this.menuList = [
      { title: "Marijuana", path: "marijuana" },
      { title: "Extracts", path: "extracts" },
      { title: "Edibles", path: "edibles" },
      { title: "Oral", path: "oral" },
      { title: "Topicals", path: "topicals" }
    ]
  }

  ngOnInit() {
    this.voucherService.getVouchersByTradeLocation("4", "4", "4", 1, 10, 1).subscribe((v) => {
      this.vouchers = v;
    })
  }

  searchIndica() {
    this.voucherService.getVouchersByTradeLocation("4", "4", "4", 1, 10, 1).subscribe((v) => {
      this.indica = v;
    })
  }
  searchSativa() {
    this.voucherService.getVouchersByTradeLocation("4", "4", "4", 1, 10, 1).subscribe((v) => {
      this.sativa = v;
    })
  }
  searchExtracts() {
    this.voucherService.getVouchersByTradeLocation("4", "4", "4", 1, 10, 1).subscribe((v) => {
      this.extracts = v;
    })
  }
  searchEdibles() {
    this.voucherService.getVouchersByTradeLocation("4", "4", "4", 1, 10, 1).subscribe((v) => {
      this.edibles = v;
    })
  }

  ngOnDestroy() {
    this.alive = false;
  }

}



