import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ViewDispensariesService } from '../view-dispensaries.service';
import { VoucherService } from '../../../../common/voucher/voucher.service';

import "rxjs/add/operator/takeWhile";

@Component({
  selector: 'home-dispensaries',
  templateUrl: './home-dispensaries.component.html',
  styleUrls: ['./home-dispensaries.component.css']
})
export class HomeDispensariesComponent implements OnInit, OnDestroy {

  private alive = true;
  private dispensarie: any;
  private vouchers: any[];


  constructor(private viewDispensariesService: ViewDispensariesService,
    private voucherService: VoucherService,

  ) { }

  ngOnInit() {
    this.viewDispensariesService.getDispensarie()
      .takeWhile(() => this.alive)
      .subscribe((res) => {
        this.dispensarie = res;
        this.getVouchers();
      })
  }

  getVouchers() {
    this.voucherService.getVouchersByTradeLocation("4", "4", this.dispensarie.vendorLocationCode, 0, 10, 1)
      .takeWhile(() => this.alive)
      .subscribe((res) => {
        console.log("res>>> ", res);
        this.vouchers = res;
      })
  }

  ngOnDestroy() {
    this.alive = false;
  }

}



