import { Injectable } from '@angular/core';
import { TradeService } from '../../../common/trade/trade.service';
import { Observable } from 'rxjs/Observable';

@Injectable()
export class ViewDispensariesService {

  private dispensarie;
  private observable;
  private tradeLocationCode;
  private alive = true;

  constructor(private tradeService: TradeService) {
  }

  public setDispensarie(tradeLocationCode) {
    this.observable = this.tradeService.getTrade(tradeLocationCode).takeWhile(() => this.alive);
    this.observable.subscribe((dispensarie) => {
      this.dispensarie = dispensarie;
      console.log("setDispensarie>>> ", this.dispensarie);
    });
  }

  public getDispensarie() {
    return this.observable;
  }

  ngOnDestroy() {
    this.alive = false;
  }
}



