import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ViewDispensariesService } from '../view-dispensaries.service';
import "rxjs/add/operator/takeWhile";

@Component({
  selector: 'view-dispensaries',
  templateUrl: './view-dispensaries.component.html',
  styleUrls: ['./view-dispensaries.component.css']
})
export class ViewDispensariesComponent implements OnInit {

  private alive = true;
  mobile = false;
  banner: any = [];
  bannerDispensaries = 'dispensaries-banner.jpg';
  images: Array<any>;

  private menuList: [{ title: string, path: string }];

  constructor(
    private route: ActivatedRoute,
    private router: Router,
    private viewDispensariesService: ViewDispensariesService
  ) {

    this.menuList = [
      { title: "Home", path: "home" },
      { title: "Overview", path: "details" },
      { title: "Reviews", path: "reviews" },
      // { title: "Strains", path: "strains" },
      // { title: "Products", path: "products" }
    ]
  }

  loadSlider() {
    // let profile = this.translate.instant("Profile");
    if (window.screen.width <= 959) {
      this.bannerDispensaries='dispensaries-banner-xs.jpg';
    }else{
      this.bannerDispensaries='dispensaries-banner.jpg';

    }
    let bannerimg = this.banner[0];
    this.images = [
      {
        "sType": "div", "styles": { "width": "100%" }, "content": `
        <div class="container-slide" style="background-image: url('/assets/images/${bannerimg}'); width:100%; background-repeat: no-repeat;background-attachment: initial;background-position: center; background-size:cover;">
      <div  class="slide-middle-txt animated">
  </div>
  </div>
      ` }];
  }

  ngOnInit() {
    this.loadSlider();
    this.route.params.takeWhile(() => this.alive)
      .subscribe((params: Params) => {
        this.viewDispensariesService.setDispensarie(params['id']);
      });
  }

  ngOnDestroy() {
    this.alive = false;
  }
}



