import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import "rxjs/add/operator/takeWhile";
import { TradeService } from '../../../../common/trade/';
import { ViewDispensariesService } from '../view-dispensaries.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
@Component({
  selector: 'review-dispensaries',
  templateUrl: './review-dispensaries.component.html',
  styleUrls: ['./review-dispensaries.component.css']
})
export class ReviewDispensariesComponent implements OnInit, OnDestroy {

  private alive = true;
  private dispensarie: any;
  reviews: Array<any>;
  reviewForm: FormGroup;
  score: number = 5;
  stars: Array<number> = [1, 2, 3, 4, 5]
  reviewAdded = false;
  reviewError = false;



  constructor(private tradeService: TradeService,
    private viewDispensariesService: ViewDispensariesService,
    private fb: FormBuilder,
    private route: ActivatedRoute) {
    this.reviewForm = fb.group({
      'text': [null, Validators.compose([Validators.required, Validators.minLength(10), Validators.maxLength(500)])],
    });
  }



  ngOnInit() {
    this.viewDispensariesService.getDispensarie()
      .takeWhile(() => this.alive)
      .subscribe((res) => {
        this.dispensarie = res;
        this.getReviews();
      })
  }
  changeScore(score) {
    this.score = score;
  }
  getReviews() {
    this.tradeService.getTradeReviews(this.dispensarie.tradeLocationCode).takeWhile(() => this.alive)
      .subscribe((res) => {
        this.reviews = res;
      });
  }
  createReview() {
    this.tradeService.createTradeReview(this.dispensarie.tradeLocationCode, this.reviewForm.value.text, this.score).takeWhile(() => this.alive)
      .subscribe((res) => {
        this.reviewForm.patchValue({ "text": "" });
        this.reviewForm.markAsPristine();
        this.reviewForm.markAsUntouched();
        this.score = 5;
        this.getReviews();
        this.reviewAdded = true;
        setTimeout(() => {
          this.reviewAdded = false;
        }, 5000);
      }, () => {
        this.reviewError = true;
        setTimeout(() => {
          this.reviewError = false;
        }, 5000);
      });
  }

  ngOnDestroy() {
    this.alive = false;
  }

}



