import { Component, OnInit, OnDestroy } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { ViewDispensariesService } from '../../view-dispensaries.service';
import { VoucherService } from '../../../../../common/voucher/voucher.service';

import "rxjs/add/operator/takeWhile";

@Component({
  selector: 'marijuana-strains',
  templateUrl: './marijuana-strains.component.html',
  styleUrls: ['./marijuana-strains.component.css']
})
export class MarijuanaStrainsComponent implements OnInit, OnDestroy {

  private dispensarie: any;
  private vouchers: any[];
  private alive = true;

  constructor(
    private voucherService: VoucherService,
    private viewDispensariesService: ViewDispensariesService) {

  }

  ngOnInit() {
    this.viewDispensariesService.getDispensarie()
      .takeWhile(() => this.alive)
      .subscribe((res) => {
        this.dispensarie = res;
        this.getVouchers();
      })
  }

  ngOnDestroy() {
    this.alive = false;
  }

  getVouchers() {
    // this.voucherService.getVouchersByTradeLocation("R", "4", this.dispensarie.vendorLocationCode, 0, 10, 1)
    this.voucherService.getVouchersByTradeLocation("4", "4", "4", 0, 10, 1)
      .takeWhile(() => this.alive)
      .subscribe((res) => {
          console.log("res>>> ",res);
        this.vouchers = res;
      })
  }

}



