import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AgmCoreModule } from '@agm/core';
import { NglModule } from 'ng-lightning/ng-lightning';

import { ViewDispensariesRoutingModule } from './view-dispensaries.routing';
import { ViewDispensariesService } from './view-dispensaries.service';

import { ViewDispensariesComponent } from './main/view-dispensaries.component';
import { DetailsDispensariesComponent } from './details/details-dispensaries.component';
import { HomeDispensariesComponent } from './home/home-dispensaries.component';
import { ReviewDispensariesComponent } from './review/review-dispensaries.component';
import { ProductsDispensariesComponent } from './products/products-dispensaries.component';
import { StrainsDispensariesComponent } from './strains/strains-dispensaries.component';
import { MarijuanaStrainsComponent } from './strains/marijuana/marijuana-strains.component';
import { ExtractsStrainsComponent } from './strains/extracts/extracts-strains.component';
import { EdiblesStrainsComponent } from './strains/edibles/edibles-strains.component';
import { OralStrainsComponent } from './strains/oral/oral-strains.component';
import { TopicalsStrainsComponent } from './strains/topicals/topicals-strains.component';
import { CardModule } from '../../../common/components/card';
import { VoucherCardModule } from '../../../common/components/voucher';
import { SliderModule } from '../../slider';

import { TradeModule } from '../../../common/trade';
import { FlexLayoutModule } from '@angular/flex-layout';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

@NgModule({
  imports: [
    CommonModule,
    ViewDispensariesRoutingModule,
    AgmCoreModule,
    NglModule,
    CardModule,
    FlexLayoutModule,
    VoucherCardModule,
    TradeModule,
    SliderModule,
    FormsModule,
    ReactiveFormsModule
  ],
  declarations: [
    ViewDispensariesComponent,
    DetailsDispensariesComponent,
    HomeDispensariesComponent,
    ProductsDispensariesComponent,
    StrainsDispensariesComponent,
    MarijuanaStrainsComponent,
    ExtractsStrainsComponent,
    EdiblesStrainsComponent,
    OralStrainsComponent,
    TopicalsStrainsComponent,
    ReviewDispensariesComponent
  ],
  providers: [
    ViewDispensariesService
  ]
})
export class ViewDispensariesModule {
}



